@use '../global' as g;

/*********************************
    ボックス
*********************************/

.box{
    background-color: g.$color_white;
    border: 1px solid g.$color_grey_light;
    border-radius: 4px;
    box-shadow: 0 2px 8px 0 rgba(#000000,0.1);
}

.box__inner{
    padding: 20px;
}